.questions {
    height: 438px;
    background: url(../img/bg/bg_bottom.jpg) center (center/cover) no-repeat;
    &__item {
        &_main {
            padding-top: 68px;
            &_header {
                width: 896px;
                height: 44px;
                margin: 0 auto;
                background-color: #25333c;
                font-size: 19px;
                font-weight: bold;
                font-stretch: normal;
                font-style: normal;
                letter-spacing: normal;
                text-align: center;
                color: #ffffff;
                span {
                    position: absolute;
                    margin: 23px 0px 0 -160px;
                    font-size: 14px;
                    font-weight: normal;
                    font-stretch: normal;
                    font-style: normal;
                    letter-spacing: normal;
                    text-align: center;
                    color: #ffffff; 
                }
            }
            &_subheader {
                font-size: 14px;
                font-weight: normal;
                font-stretch: normal;
                font-style: normal;
                letter-spacing: normal;
                text-align: center;
                color: #ffffff;
            }
        }
        &_form {
            width: 896px;
            height: 250px;
            margin: 0 auto;
            padding-bottom: 1px;
            background-color: #ffffff8a;
            .input_text {
                width: 268px;
                height: 45px;
                margin: 30px 0px 0 22px;
                padding-left: 5px;
                border: solid 1px rgba(0, 0, 0, 0.21);
                border-radius: 20px 20px 20px 20px;
                background-color: #ffffff;
            }
            .input_textarea {
                width: 856px;
                height: 71px;
                margin: 8px 0 0 20px;
                border: none;
                border-radius: 8px;
                padding-left: 4px;
            }
            &_btn {
                width: 180px;
                height: 45px;
                display: block;
                margin: 17px auto;
                padding: 5px 23px;
                background-color: #ec644b;
                border-color:#cc523c;
                border-radius: 20px 20px 20px 20px;
                font-size: 12px;
                font-weight: bold;
                font-stretch: normal;
                font-style: normal;
                line-height: 1.17;
                letter-spacing: normal;
                text-align: center;
                color: #ffffff;
                text-transform: uppercase;
                &:hover {
                    box-shadow: 5px 3px 4px 2px #c73e26;
                    transition: 0.5s all;
                }  
            }
        }

    }
}