.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0, 0.8);
    &__content {
        background-color: #fff;
        margin: 15% auto;
        padding: 20px;
        width: 293px;
        max-width: 100%;
        
    }
    &__close {
        margin-top: -23px;
        float: right;
        font-size: 28px;
        cursor: pointer;
    }
    &__subtitle {
        text-align: center;
        font-size: 21px;
    }
    &__form {
        &__main {
            text-align: center;
        }
        &__control {
            display: inline-block;
            width: 100%;
            margin-top: 42px;
            border: none;
            border-bottom: 1px solid #685f5f;
        }
    }
    &__btn {
        width: 230px;
        height: 57px;
        display: block;
        margin: 39px auto;
        padding: 5px 23px;
        background-color: #ec644b;
        border-color: #cc523c;
        border-radius: 20px 20px 20px 20px;
        font-size: 15px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.17;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        text-transform: uppercase;
        &:hover {
            box-shadow: 5px 3px 4px 2px #c73e26;
            transition: 0.5s all;
        } 
    }
}

.fade-in {
    animation: fadeIn ease 1s;
    -webkit-animation: fadeIn ease 1s;
    -moz-animation: fadeIn ease 1s;
    -o-animation: fadeIn ease 1s;
    -ms-animation: fadeIn ease 1s;
}
  
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-moz-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-o-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-ms-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}