.agent {
    height: 342px;
    background: url('../img/bg/bg_middle.jpg') center (center/cover) no-repeat;
    &__picture {
        display: block;
        margin: 0 auto;
        padding-top: 58px;
    }
    &__header {
        margin: 20px auto;
        font-size: 26px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 0.88;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
    &__subheader {
        width: 65%;
        margin: 20px auto;
        font-size: 19px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
}