hr {
    width: 80%;
    margin: 0 auto;
}
.top {
   height: 680px;
   background: url('../img/bg/bg_top.jpg') center (center/cover) no-repeat; 
    &__logo {
        margin-top: 23px;
    }
    &__menu {
        display: flex;
        margin: -94px 0 0 83px;
        &_item {
            margin: 37px 0px 0px 23px;
            padding-right: 25px;
            font-size: 15px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            letter-spacing: normal;
            color: #ffffff;
            border-right: 1px dotted #ffffff;
            &:hover {
                color: #ec644b;
                transition: 0.5s all;
            }
            &:last-child {
                border: none;
            }
        }
    }
    &__questions {
        margin: -38px 169px 0 0;
        font-size: 14px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: right;
        color: #ffffff;  
    }
    &__header {
        margin: 78px 87px 26px 94px;
        font-size: 24px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
    &__subheader {
        margin: 26px 61px 9px 41px;
        font-size: 72px;
        font-weight: bold;
        font-stretch: condensed;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ec644b;
    }
    &__descr {
        margin: 9px 53px 38px 62px;
        font-size: 42px;
        font-weight: normal;
        font-stretch: condensed;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
    &__wrapper {
        margin-top: 27px;
        display: grid;
        grid-template-columns: repeat(4,250px);
        column-gap: 41px;
    }
    &__item {
        &__picture {
            margin: 0 0 13px 92px;
        }
        &__descr {
            font-size: 15px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            letter-spacing: normal;
            text-align: center;
            color: #ffffff;
        }
    }
}

.btn {
    &_right {
        width: 160px;
        height: 45px;
        margin: -18px 0 0 0;
        padding: 5px 23px;
        background-color: #ec644b;
        border-color:#cc523c;
        border-radius: 20px 20px 20px 20px;
        font-size: 11px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        text-transform: uppercase;
        float: right;
        &:hover {
            box-shadow: 5px 3px 4px 2px #c73e26;
            transition: 0.5s all;
        }
    }
    &__payment {
        width: 230px;
        height: 57px;
        display: block;
        margin: 39px auto;
        padding: 5px 23px;
        background-color: #ec644b;
        border-color:#cc523c;
        border-radius: 20px 20px 20px 20px;
        font-size: 15px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.17;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        text-transform: uppercase;
        &:hover {
            box-shadow: 5px 3px 4px 2px #c73e26;
            transition: 0.5s all;
        } 
    }
}

