.advantages {
    height: 687px;
    padding-top: 51px;
    background-color: #f3f3f3;
    &__header {
        font-size: 26px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #2e2e2e;
    }
    &__wrapper {
        display: grid;
        margin: 21px 0 110px 0;
        grid-template-columns: 450px 450px;
        grid-template-rows: repeat(4,100px);
        gap: 27px;
        justify-content: center; 
    }
    &__picture {
        float: left;
    }
    &__name {
        padding-left: 73px;
        font-size: 16px;
        line-height: 2;
        font-weight: bold;
        color: #ec644b;
    }
    &__descr {
        padding-left: 74px;
        font-size: 14px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        color: #3a3a3a;
    }
}