.scheme {
    padding-top: 51px;
    padding-bottom: 46px;
    background-color: #f3f3f3;

    &__header {
        font-size: 26px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #2e2e2e;
    }
    &__wrapper_top {
        display: grid;
        grid-template-columns: repeat(4,250px);
        justify-content: center;
        align-items: center;
        column-gap: 28px;
    }
    &__item {
        &__picture {
            display: block;
            margin: 12px auto;
            justify-content: center;
            align-items: center;
        }
        &__header {
            font-size: 16px;
            font-weight: bold;
            text-align: center;
            color: #ec644b;
        }
        &__descr {
            font-size: 14px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            letter-spacing: normal;
            text-align: center;
            color: #3a3a3a;
        }
    }
    &__wrapper_bottom {
        display: grid;
        padding-top: 20px;
        grid-template-columns: repeat(3,250px);
        justify-content: center;
        align-items: center;
        column-gap: 28px;
    }
    &__item {
        &__picture {
            display: block;
            margin: 12px auto;
            justify-content: center;
            align-items: center;
        }
        &__header {
            font-size: 16px;
            font-weight: bold;
            text-align: center;
            color: #ec644b;
        }
        &__descr {
            font-size: 14px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            letter-spacing: normal;
            text-align: center;
            color: #3a3a3a;
        }
    }
}