.dignities {
    &__header {
        margin-top: 51px;
        font-size: 26px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #2e2e2e;
    }
    &__subheader {
        margin: 18px 195px 37px 158px;
        font-size: 21px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #2e2e2e;
    }
    &__wrapper {
        display: grid;
        grid-template-columns: 360px 360px 360px;
        column-gap: 29px;
        justify-content: center;
    }
    &__item {
        &__name {
            margin-top: 27px;
            font-size: 18px;
            font-weight: bold;
            text-align: center;
            color: #ec644b;
        }
        &__descr {
            margin-bottom: 57px;
            font-size: 14px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.57;
            letter-spacing: normal;
            color: #3a3a3a;
        }
    }
}
