@import 'libs/bootstrap-reboot.min.scss';
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;900&display=swap');
* {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    width: 1140px;
    margin: 0 auto;
}

.line_orange {
    width: 148px;
    height: 1px;
    margin: 20px auto;
    color: orangered;
}

.phone {
    margin: -22px 168px -8px -330px;
    font-size: 17px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 4;
    letter-spacing: normal;
    color: #ffffff;
    float: right;
    &:hover {
        color: #ec644b;
        transition: 0.5s all;
    }
}

.map {
    height: 500px;
}

