@media (max-width: 1200px) {
    .container {
        max-width: 960px;
    }
    .top {
        max-width: 100%;
        &__menu {
            margin: -94px 0 0 92px;
            &_item {
                margin: 37px 0px 0 10px;
            }
        }
        &__subheader {
            font-size: 48px;
        }
        &__descr {
            font-size: 35px;
        }
        &__questions {
            margin: -38px 14px 0 0;
        }
        &__wrapper {
            column-gap: 0;
            grid-template-columns: repeat(4,244px);
            align-items: baseline;
        }
    }
    .dignities {
        &__wrapper {
            display: block;
        }
        &__picture {
            display: block;
            margin: 0 auto;
        }
        &__item {
            &__name {
                font-size: 28px;
            }
            &__descr {
                font-size: 18px;
                text-align: center;
            }
        }
    }
    .prices {
        &__wrapper {
            display: block;
            margin-top: 34px;
            margin-bottom: 56px;
        }
        &__item {
            &__name {
                margin: auto;
            }
            &__descr {
                width: 22%;
                margin: 0 auto;
            }
        }
    }
    .scheme {
        &__wrapper {
            &_top {
                grid-template-columns: repeat(4,245px);
                justify-content: center;
                align-items: baseline;
                column-gap: 0;
            }
        }
    }
    .footer {
        &__questions {
            margin: -42px 14px 0 0;
        }
        &__line {
            width: 100%;
            margin: 79px 0 19px;
        }
    }
    .phone {
        margin: -22px 11px -8px -330px;
    }
    .btn_right {
        margin: 27px 0 0 0;
    }
}

@media (max-width: 991px) {
    .container {
        max-width: 100%;
    }
    .top {
        &__questions {
            margin: -76px 14px 0 0;
        }
        &__header {
            font-size: 22px;
        }
        &__subheader {
            font-size: 46px;
        }
        &__descr {
            font-size: 28px;
        }
        &__wrapper {
            grid-template-columns: repeat(4,192px);
        }
        &__item {
            &__picture {
                margin: 10px auto;
                display: block;
            }
            &__descr {
                font-size: 13px;
            }
        }
    }
    .advantages {
        &__wrapper {
            grid-template-columns: 370px 370px;
        }
    }
    .scheme {
        &__wrapper {
            &_top {
                grid-template-columns: repeat(4,190px);
            }
            &_bottom {
                grid-template-columns: repeat(3,230px);
            }
        }
    }
    .questions {
        &__item {
            &_main {
                &_header {
                    width: 100%;
                }
            }
            &_form {
                width: 100%;
                .input_text {
                    width: 30%;
                }
                .input_textarea {
                    width: 95%;
                }
            }
        }
    }
    .btn_right {
        display: none;
    }
}
@media (max-width: 767px) {
    .top {
