            width: 100%;
        }
    }
    .managers {
        &__subheader {
            width: 100%;
        }
    }
    .scheme {
        &__wrapper {
            &_top {
                grid-template-columns: repeat(2,190px);
                row-gap: 33px;
            }
            &_bottom {
                grid-template-columns: repeat(2,230px);
                row-gap: 33px;
            }
        }
    }
    .questions {
        &__item {
            &_form {
