.reviews {
    min-height: 470px;
    padding: 51px 0 65px 0;
    &__header {
        color: #2e2e2e;
        font-size: 26px;
        font-weight: 700;
        line-height: 23px;
        text-transform: uppercase;
        text-align: center;
    }
    #reviews__hr {
        margin-top: 20px;
        width: 148px;
        border-bottom: 1px solid #ec644b;
        border-top: none;
    }
    &__subheader {
        margin-top: 17px;
        color: #2e2e2e;
        font-size: 21px;
        font-weight: 300;
        line-height: 24px;
        text-align: center;
    }
    &__arrow {
        &_left {
            width: 10%;
            display: inline-block;
            margin: -308px 0 0 197px;
            cursor: pointer;
        }
        &_right {
            width: 10%;
            display: inline-block;
            margin: -205px 205px 0 0px;
            float: right;
            cursor: pointer;  
        }
    }
    &__slider {
        &__content {
            padding-top: 25px;
            margin: 0 auto;
            width: 33%;
            &__picture {
                display: block;
                width: 30%;
                margin: 0 auto;
            }
            &__name {
                padding: 10px 0 10px 0;
                font-size: 18px;
                font-weight: 700;
                text-align: center;
            }
            &__text {
                color: #3a3a3a;
                font-size: 14px;
                font-weight: 300;
            }
        }
    }
}

.fadeIn{
    animation-name: fadeIn;
    -webkit-animation-name: fadeIn; 
 
    animation-duration: 0.5s; 
    -webkit-animation-duration: 0.5s;
 
    animation-timing-function: ease-in-out; 
    -webkit-animation-timing-function: ease-in-out;     
 
    visibility: visible !important; 
}
 
@keyframes fadeIn {
    0% {
        transform: scale(0);
        opacity: 0.0;       
    }
    60% {
        transform: scale(1.1);  
    }
    80% {
        transform: scale(0.9);
        opacity: 1; 
    }   
    100% {
        transform: scale(1);
        opacity: 1; 
    }       
}
 
@-webkit-keyframes fadeIn {
    0% {
        -webkit-transform: scale(0);
        opacity: 0.0;       
    }
    60% {
        -webkit-transform: scale(1.1);
    }
    80% {
        -webkit-transform: scale(0.9);
        opacity: 1; 
    }   
    100% {
        -webkit-transform: scale(1);
        opacity: 1; 
    }       
}