.managers {
    height: 386px;
    background: url(../img/bg/bg_managers.jpg) center (center/cover) no-repeat;
    &__picture {
        display: block;
        margin: 58px auto;
        padding-top: 58px;
    }
    &__header {
        margin-top: 20px;
        font-size: 26px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
    &__subheader {
        width: 75%;
        margin: 18px auto;
        font-size: 19px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
    }
}