.consultation {
    &__header {
        font-size: 28px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #3a3a3a;
    }
    &__picture {
        display: block;
        position: absolute;
        margin-top: 20px;
        left: 50%;
        transform: translateX(-50%);
    }
    &__wrapper {
        margin-top: 80px;
        height: 166px;
        border: 1px solid #898989;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: -1;
        &__form {
            width: 268px;
            height: 45px;
            margin: 0 6px 0 0;
            padding-left: 5px;
            border: solid 1px rgba(0, 0, 0, 0.21);
            border-radius: 20px 20px 20px 20px;
            background-color: #ffffff;
        }
    }
    &__btn {
        width: 180px;
        height: 45px;
        display: block;
        position: absolute;
        margin-top: 162px;
        left: 50%;
        transform: translateX(-50%);
        padding: 5px 23px;
        background-color: #ec644b;
        border-color:#cc523c;
        border-radius: 20px 20px 20px 20px;
        font-size: 12px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.17;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        text-transform: uppercase;
        &:hover {
            box-shadow: 5px 3px 4px 2px #c73e26;
            transition: 0.5s all;
        }  
    }
}