.prices {
    &__header {
        margin-top: 53px;
        font-size: 26px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 0.88;
        letter-spacing: normal;
        text-align: center;
        color: #2e2e2e;
    }
    &__subheader {
        width: 47%;
        margin: 17px auto;
        font-size: 21px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.14;
        letter-spacing: normal;
        text-align: center;
        color: #2e2e2e;
    }
    &__wrapper {
        display: grid;
        margin-top: 34px;
        margin-bottom: 56px;
        grid-template-columns: repeat(4,265px);
        column-gap: 28px;
        justify-content: center;
    }
    &__item {
        &__name {
            width: 261px;
            height: 61px;
            margin: 0 0 14px;
            padding-top: 19px;
            background-color: #25333c;
            font-size: 16px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            letter-spacing: normal;
            text-align: center;
            text-transform: uppercase;
            color: #ffffff;
        }
        &__picture {
            display: block;
            margin: 11px auto;
        }
        &__header {
            margin-top: 12px;
            font-size: 14px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            letter-spacing: normal;
            text-align: center;
            color: #2e2e2e;
        }
        &__subheader {
            font-size: 28px;
            text-align: center;
            color: #ec644b;
        }
        &__descr {
            font-size: 14px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            letter-spacing: normal;
            text-align: center;
            color: #2e2e2e;
        }
    }
    &__btn {
        width: 160px;
        height: 45px;
        display: block;
        margin: 25px auto;
        padding: 5px 23px;
        background-color: #ec644b;
        border-color:#cc523c;
        border-radius: 20px 20px 20px 20px;
        font-size: 15px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.17;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        text-transform: uppercase;
        &:hover {
            box-shadow: 5px 3px 4px 2px #c73e26;
            transition: 0.5s all;
        }  
    }
}
