.footer {
    height: 206px;
    background-color: #25333c;
    &__logo {
        margin-top: 24px;
        float: left;
    }
    &__header {
        padding-top: 60px;
        margin-left: 134px;
        font-size: 16px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
    }
    &__questions {
        margin: -42px 164px 0 0;
        font-size: 14px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: right;
        color: #ffffff;  
    }
    &__line {
        width: 1171px;
        height: 1px;
        margin: 70px 0 19px;
    }
    &__policy {
        margin: 20px auto;
        display: block;
        font-size: 13px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff;
        &:hover {
            transition: 0.5s all;
            color: #ec644b;
        }
    }
}